    /**
     * @noteType component
     * @name Image
     * @tagName mt-image
     * @desc 图片
     * @wrapClassName mint-image
     * @html
     * <mt-image src="http://res.wisedu.com/fe_components/images/errorTip/System_upgrade.png"></mt-image>
     */
export default {
  name: 'mt-image',
    methods: {
        handleClick(evt) {
            this.$emit('click', evt);
        }
    },
  props: {
      /**
       * @noteType prop
       * @field src
       * @desc 地址
       * @type input
       * @value http://res.wisedu.com/fe_components/images/errorTip/System_upgrade.png
       */
      src: String,
      /**
       * @noteType prop
       * @field width
       * @desc 宽度
       * @type input
       * @value
       */
      width: String,
      /**
       * @noteType prop
       * @field height
       * @desc 高度
       * @type input
       * @value
       */
      height: String
  },
    computed: {
        computeStyle: function() {
            let width = this.width;
            let height = this.height;
            let _style = '';
            if (width) {
                _style += 'width:' + width + ';';
            }
            if (height) {
                _style += 'height:' + height + ';';
            }
            return _style;
        }
    }
};



