/* eslint-disable */
import axios from 'axios'

export default {
  methods: {
    selectClick (item) {
      this.value = item
      if (window.location.hash.indexOf('smile-select') > -1) {
        history.back()
      }
    },
    loadOptions (pId) {
      axios.get('/mock/tree.json?pId=' + pId).then(resp => {
        let respData = resp.data
        if (respData.code == '0') {
          if (pId === '' || pId.length === 0) {
            this.options = respData.datas ? respData.datas.code.rows : []
          } else {
            let options = this.options
            options.filter(item => item.id === pId)[0].children = respData.datas ? respData.datas.code.rows : []
            this.$set(this, 'options', [])
            this.$nextTick(_ => {
              this.$set(this, 'options', options)
            })
          }
        }
      })
    },
    handleDicSelectorClick () {
      axios.get('/mock/dic.json').then(resp => {
        let respData = resp.data
        if (respData.code == '0') {
          // this.$set(this.asyncDicSlot[0], 'values', respData.data)
          this.asyncDicSlot[0].values = respData.data
        }
        console.log(resp)
      })
    }
  },

  data() {
    return {
      value: '',
      options: []
    };
  },

  mounted() {
  }
};



