    /**
     * @noteType component
     * @name Text
     * @tagName mt-text
     * @desc 文本
     * @wrapClassName mint-text
     * @html
     * <mt-text>{{text}}</mt-text>
     */
export default {
  name: 'mt-text',
    methods: {
        handleClick(evt) {
            this.$emit('click', evt);
        }
    },
  props: {
      size: String,
      /**
       * @noteType prop
       * @field type
       * @desc 样式
       * @type select
       * @value default
       * @option
       [
       {
         "text": "default",
         "value": "default"
       },
       {
         "text": "primary",
         "value": "primary"
       },
       {
         "text": "warning",
         "value": "warning"
       },
       {
         "text": "danger",
         "value": "danger"
       },
       {
         "text": "grey",
         "value": "grey"
       }
       ]
       */
      type: {
        type: String,
        default: 'default'
      },
      color: String
  },
    computed: {
        computeStyle: function() {
            let _style = '';
            let size = this.size;
            let color = this.color;
            if (size) {
                _style += `font-size: ${size};`;
            }

            if (color) {
                _style += `color: ${color};`;
            }

            return _style;
        }
    }
};

    /**
     * @noteType external
     * @content
     {
       "mock": {
          "text": "文本"
        }
     }
     */



